// components/StatusForm.tsx
import { useState } from "react";
import CustomDropdown from "../common/CustomDropdown";

interface StatusFormProps {
  onSearch: (data: any) => void;
}

interface DropdownOption {
  label: string;
  value: string;
}

const StatusForm = ({ onSearch }: StatusFormProps) => {
  const [formData, setFormData] = useState({
    refNo: "",
    branch: "",
    agent: "",
    billTo: "",
    referer: "",
    applicant: "",
    country: "",
    visaType: "",
    contactNo: "",
    status: "",
    dateFrom: "",
    dateTo: "",
    otherOption: "",
  });

  const createOptions = (items: string[]): DropdownOption[] => {
    return items.map(item => ({ label: item, value: item }));
  };

  const branchOptions = createOptions(["Branch 1", "Branch 2", "Branch 3"]);
  const agentOptions = createOptions(["Agent 1", "Agent 2", "Agent 3"]);
  const billToOptions = createOptions(["Company 1", "Company 2", "Company 3"]);
  const refererOptions = createOptions(["Referer 1", "Referer 2", "Referer 3"]);
  const countryOptions = createOptions(["United States", "United Kingdom", "Canada"]);
  const visaTypeOptions = createOptions(["Tourist", "Business", "Student"]);
  const statusOptions = createOptions(["Pending", "Processing", "Approved", "Rejected"]);

  const handleChange = (field: string, value: string) => {
    setFormData({
      ...formData,
      [field]: value,
    });
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSearch(formData);
  };

  return (
    <div className="px-6 py-6">
      <h3 className="text-lg font-medium text-gray-900 pb-3 border-b border-[#E6EAF2] mb-6">
        Check Realtime Status Online
      </h3>

      <form onSubmit={handleSubmit} className="space-y-6">
        <div className="grid grid-cols-5 gap-5">
          <div>
            <label className="block text-xs text-[#696969] mb-1">
              Reference No.
            </label>
            <input
              type="text"
              value={formData.refNo}
              onChange={(e) => handleChange("refNo", e.target.value)}
              className="w-full h-10 px-3 border border-[#E6EAF2] rounded focus:outline-none focus:ring-1 focus:ring-blue-500 text-sm"
              placeholder="Enter ref no."
            />
          </div>

          <div>
            <label className="block text-xs text-[#696969] mb-1">Branch</label>
            <CustomDropdown
              options={branchOptions}
              value={formData.branch || ''}
              onChange={(value) => handleChange("branch", value)}
              placeholder="Select"
              className="h-10"
            />
          </div>

          <div>
            <label className="block text-xs text-[#696969] mb-1">
              Agent / Corporate
            </label>
            <CustomDropdown
              options={agentOptions}
              value={formData.agent || ''}
              onChange={(value) => handleChange("agent", value)}
              placeholder="Select"
              className="h-10"
            />
          </div>

          <div>
            <label className="block text-xs text-[#696969] mb-1">
              Bill to Company
            </label>
            <CustomDropdown
              options={billToOptions}
              value={formData.billTo || ''}
              onChange={(value) => handleChange("billTo", value)}
              placeholder="Select"
              className="h-10"
            />
          </div>

          <div>
            <label className="block text-xs text-[#696969] mb-1">Referer</label>
            <CustomDropdown
              options={refererOptions}
              value={formData.referer || ''}
              onChange={(value) => handleChange("referer", value)}
              placeholder="Select"
              className="h-10"
            />
          </div>
        </div>

        <div className="grid grid-cols-5 gap-5">
          <div>
            <label className="block text-xs text-[#696969] mb-1">
              Applicant Name
            </label>
            <input
              type="text"
              value={formData.applicant}
              onChange={(e) => handleChange("applicant", e.target.value)}
              className="w-full h-10 px-3 border border-[#E6EAF2] rounded focus:outline-none focus:ring-1 focus:ring-blue-500 text-sm"
              placeholder="Enter name"
            />
          </div>

          <div>
            <label className="block text-xs text-[#696969] mb-1">Country</label>
            <CustomDropdown
              options={countryOptions}
              value={formData.country || ''}
              onChange={(value) => handleChange("country", value)}
              placeholder="Select"
              className="h-10"
            />
          </div>

          <div>
            <label className="block text-xs text-[#696969] mb-1">
              Visa Type
            </label>
            <CustomDropdown
              options={visaTypeOptions}
              value={formData.visaType || ''}
              onChange={(value) => handleChange("visaType", value)}
              placeholder="Select"
              className="h-10"
            />
          </div>

          <div>
            <label className="block text-xs text-[#696969] mb-1">
              Contact No.
            </label>
            <input
              type="text"
              value={formData.contactNo}
              onChange={(e) => handleChange("contactNo", e.target.value)}
              className="w-full h-10 px-3 border border-[#E6EAF2] rounded focus:outline-none focus:ring-1 focus:ring-blue-500 text-sm"
              placeholder="Enter number"
            />
          </div>

          <div>
            <label className="block text-xs text-[#696969] mb-1">Status</label>
            <CustomDropdown
              options={statusOptions}
              value={formData.status || ''}
              onChange={(value) => handleChange("status", value)}
              placeholder="Select"
              className="h-10"
            />
          </div>
        </div>

        <div className="grid grid-cols-5 gap-5">
          <div>
            <label className="block text-xs text-[#696969] mb-1">
              Date From
            </label>
            <input
              type="date"
              value={formData.dateFrom}
              onChange={(e) => handleChange("dateFrom", e.target.value)}
              className="w-full h-10 px-3 border border-[#E6EAF2] rounded focus:outline-none focus:ring-1 focus:ring-blue-500 text-sm"
            />
          </div>

          <div>
            <label className="block text-xs text-[#696969] mb-1">Date To</label>
            <input
              type="date"
              value={formData.dateTo}
              onChange={(e) => handleChange("dateTo", e.target.value)}
              className="w-full h-10 px-3 border border-[#E6EAF2] rounded focus:outline-none focus:ring-1 focus:ring-blue-500 text-sm"
            />
          </div>

          <div className="col-span-2"></div>

          <div className="flex justify-end items-end">
            <button
              type="submit"
              className="h-10 bg-[#0B498B] text-white px-5 rounded"
            >
              Check Status
            </button>
          </div>
        </div>
      </form>
    </div>
  );
};

export default StatusForm;
